import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {}
	},
	onShow() {
		uni.hideKeyboard()
		this.$store.commit('SET_LTABBAR', 'user');
	},
	onLoad() {
		uni.hideTabBar()
	},
	methods: {
		applyTo() {
			uni.navigateTo({
				url: '/pages/job/apply'
			});
		},
		focusTo() {
			uni.navigateTo({
				url: '/pages/company/focus'
			});
		},
		articleTo(i) {
			uni.navigateTo({
				url: '/pages/article/detail?id=' + i
			});
		},
		securityTo() {
			uni.navigateTo({
				url: '/pages/my/security'
			});
		},
		trainingTo(){
			uni.navigateTo({
				url: '/pages/training/apply'
			});
		},
		acTo() {
			uni.navigateTo({
				url: '/pages/my/ac'
			});
		},
		helpTo() {
			uni.navigateTo({
				url: '/pages/article/help'
			});
		},
		collectionTo() {
			uni.navigateTo({
				url: '/pages/job/collection'
			});
		}
	}
}
